import React from "react";

import { PopupMixpanelService } from '../../../services/popup-mixpanel.service';
import { MessageSender } from "../../../../common/messaging/message-sender";
import { RequestSource } from "../../../../common/interfaces/login";
import { PopupScreen } from "../../PopupScreen";

import './LoginScreen.scss';


// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================
interface LoginScreenProps { }
interface LoginScreenState { }

// ===========================================================================================
// --- LoginScreeen
// ===========================================================================================
export class LoginScreen extends PopupScreen<LoginScreenProps, LoginScreenState> {

    constructor(props: LoginScreenProps) {
        super(props, 'login');
    }

    componentDidMount = async () => {
        super.componentDidMount();
    }

    handleLoginClick = async () => {
        PopupMixpanelService.trackLoginButtonPressed();
        MessageSender.goToLogin(false, RequestSource.POPUP);
    }

    handleSignupClick = async () => {
        PopupMixpanelService.trackSignupButtonPressed();
        MessageSender.goToLogin(true, RequestSource.POPUP);
    }

    renderSignedOutDefault(): any {
        const canvas = {
            width: '450px',
            height: '580px',
        };

        const buttonBar = {
            bottom: '39px',
            padding: '0px 50px'
        }

        return (
            <div className="canvas" style={canvas}>
                <div className="splash">
                    <img src="images/login/chrome-ext-splash.gif" alt="banner-retailers"></img>
                </div>
                <div className="login-retailer-logo earny">
                    <img src="images/earny-logo.svg" alt="earny-logo" />
                </div>
                <div>
                    <p className="signup-text">Sign up to start earning cashback when you shop on the web or on our app</p>
                </div>
                <div className="button-bar-two" style={buttonBar}>
                    <button id="signin-button" className="left" onClick={this.handleLoginClick}>
                        Log In
                    </button>
                    <button id="signup-button" className="right" onClick={this.handleSignupClick}>
                        Sign Up
                    </button>
                </div>
            </div>
        );
    }

    render() {
        return this.renderSignedOutDefault();
    }
}