import React from "react";

import { NavLink } from 'react-router-dom';

import './BottomNavigation.scss';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================
export interface BottomNavigationRoute {
    key: number;
    path: string;
    iconPathUrl: string;
}

interface BottomNavigationProps {
    routes: BottomNavigationRoute[];
}

interface BottomNavigationState {

}

// ===========================================================================================
// --- BottomNavigation
// ===========================================================================================
export class BottomNavigation extends React.Component<BottomNavigationProps, BottomNavigationState> {

    render() {
        const menuItems: any = [];

        this.props.routes.map((route: any) => {
            return menuItems.push(
                <div key={route.key} className="bottom-nav-menu-item">
                    <NavLink to={route.path} activeClassName='navlink-active'>
                        <img src={route.iconPathUrl} alt=""/>
                    </NavLink>
                </div>
            )
        })

        return (
            <div className="bottom-navigation">
                {menuItems}
            </div>
        )
    }
}
