import React from 'react';

import { PopupMixpanelService } from '../../../services/popup-mixpanel.service';
import { Retailer, CashbackList } from '../../../../common/interfaces';
import { MessageSender } from '../../../../common/messaging/message-sender';
import { CashbackListComponent } from './CashbackList/CashbackList';
import { PopupScreen } from '../../PopupScreen';
import { RetailerActivatedComponent } from './RetailerActivated/RetailerActivatedComponent';
import { RequestSource } from '../../../../common/interfaces/login';

import './HomeScreen.scss';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================

interface HomeScreenProps {

}

interface HomeScreenState {
  loaded: boolean,
  activeRetailer: Retailer | null,
  cashbackList: CashbackList | null;
  canActivateActiveRetailer: boolean,
  isActiveRetailerActivated: boolean
}

const RetailerLogo: React.FC<{ imageUrl: string; }> = (props) => {
  const imageUrl = props.imageUrl;

  const isEarnyLogo = imageUrl.includes('earny-logo-eyebrows');
  const className = isEarnyLogo ? 'home-earny-logo' : 'home-retailer-logo';

  return (
    <div className={className}>
      <img src={props.imageUrl} alt="retailer-logo" />
    </div>
  )
}

// ===========================================================================================
// --- HomeScreen
// ===========================================================================================
export class HomeScreen extends PopupScreen<HomeScreenProps, HomeScreenState> {


  constructor(props: HomeScreenProps) {
    super(props, 'home');

    this.state = {
      loaded: false,
      activeRetailer: null,
      cashbackList: null,
      canActivateActiveRetailer: false,
      isActiveRetailerActivated: false,
    };
  }

  async componentDidMount() {
    super.componentDidMount();

    let isActiveRetailerActivated: boolean = false;
    let canActivateActiveRetailer: boolean = false;

    try {

      const [activeRetailer, cashbackList] = await Promise.all([
        MessageSender.getActiveRetailer(),
        MessageSender.getCashbackList()
      ])

      if (activeRetailer) {
        isActiveRetailerActivated = activeRetailer.isCashbackActivated || false;
        canActivateActiveRetailer = !isActiveRetailerActivated;
      }

      this.setState({
        loaded: true,
        isActiveRetailerActivated,
        activeRetailer,
        cashbackList,
        canActivateActiveRetailer,
      });
    } catch (error) {
      console.log('Error', error);
    }
  }


  renderCanActivate(activeRetailer: Retailer) {
    return (
      <div id="home-canvas">
        <RetailerLogo imageUrl={activeRetailer.retailerImageUrl} />
        <button className="home-activate-button" onClick={this.activateActiveRetailer}>{activeRetailer.buttonText}</button>
        <div className="home-activate-text">
          <img src="images/home/light-bulb-icon.png" alt="light-bulb"></img>
          <p>Activate before checkout in order to get cashback</p>
        </div>

        {activeRetailer.activateCashbackSubscribedCTA &&
          <div className="activate-double-cashback">
            <span className="double-cashback-title">{activeRetailer.activateCashbackSubscribedCTA}</span>
            <div className="switch" onClick={this.activateDoubleCashback}>
              <div className="switch-ball"></div>
            </div>
          </div>
        }
      </div>
    )
  }

  renderNoCashbackRetailerList(cashbackList: CashbackList) {
    return (
      <div id="home-canvas" className="scroll-child">
        <CashbackListComponent cashbackList={cashbackList} />
      </div>
    );
  }

  renderActivated(activeRetailer: Retailer) {
    return (
      <div id="home-canvas" className="scroll-child">
        <RetailerActivatedComponent retailer={activeRetailer} />
      </div>
    )
  }

  renderNoCashbackAvailable() {
    return (
      <div id="home-canvas">
        <div className="home-earny-logo">
          <img src="images/earny-logo.svg" alt="earny-logo" />
        </div>
        <div className="home-no-cashback">
          <img src="images/home/no-cashback-icon.svg" alt="no-cashback-icon" width="100" />
        </div>
        <div className="home-no-cashback-description">
          <p>This retailer doesn’t have any cashback offers</p>
        </div>
      </div>
    )
  }

  activateActiveRetailer = async () => {
    if (!this.state.activeRetailer) return;

    try {
      PopupMixpanelService.trackActivateCashbackPressed(this.state.activeRetailer);
      const source = RequestSource.POPUP;
      const showConfirmation = false;
      MessageSender.activateCashbackForActiveRetailer(source, showConfirmation)
      this.setState({
        isActiveRetailerActivated: true,
        canActivateActiveRetailer: false
      });

    } catch (error) {
      console.log('Error', error);
    }
  }

  activateDoubleCashback = async () => {
    if (!this.state.activeRetailer) return;

    try {
      PopupMixpanelService.trackActivateCashbackUpsellPressed(this.state.activeRetailer);

      const source = RequestSource.POPUP;
      const showConfirmation = false;
      MessageSender.activateDoubleCashbackForActiveRetailer(source, showConfirmation);

      window.close();

    } catch (error) {
      console.log('Error', error);
    }
  }

  render() {
    if (this.state.loaded === false) {
      return (<></>);
    }

    const activeRetailer = this.state.activeRetailer;

    if (activeRetailer && this.state.isActiveRetailerActivated) {
      return this.renderActivated(activeRetailer);
    }
    else if (activeRetailer && this.state.canActivateActiveRetailer) {
      return this.renderCanActivate(activeRetailer);
    }
    else if (this.state.cashbackList) {
      return this.renderNoCashbackRetailerList(this.state.cashbackList);
    }
    else {
      return this.renderNoCashbackAvailable();
    }
  }
}