import React from "react";
import { WatchlistItem } from "../../../../../common/interfaces";
import styled from 'styled-components';

import './WatchlistItemComponent.scss';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================
interface WatchlistItemComponentProps {
    isDeleteMode: boolean;
    watchlistItem: WatchlistItem;
    onItemClicked: (item: WatchlistItem) => void;
    onSelected: (item: WatchlistItem) => void;
    onUnselected: (item: WatchlistItem) => void;
}

interface WatchlistItemComponentState {
    selected: boolean;
}

const classNames = require('classnames');

// ===========================================================================================
// --- WatchlistItemComponent
// ===========================================================================================
export class WatchlistItemComponent extends React.Component<WatchlistItemComponentProps, WatchlistItemComponentState> {

    constructor(props: WatchlistItemComponentProps) {
        super(props);

        this.state = {
            selected: false
        };
    }

    componentWillReceiveProps(props: WatchlistItemComponentProps) {
        if (props.isDeleteMode === false && this.state.selected === true) {
            this.setState({ selected: false });
        }
    }

    onClickSelect = () => {
        if (this.props.isDeleteMode === false) {
            return this.props.onItemClicked(this.props.watchlistItem);
        }

        const willBeSelected = !this.state.selected;
        this.setState({ selected: willBeSelected });

        if (willBeSelected) {
            this.props.onSelected(this.props.watchlistItem);
        } else {
            this.props.onUnselected(this.props.watchlistItem);
        }
    }

    render() {

        const productName: string = (this.props.watchlistItem.name.length >= 45) ? (this.props.watchlistItem.name.substring(0, 45) + '...') : this.props.watchlistItem.name;

        const PriceDropValue = styled.span`color: #${this.props.watchlistItem.priceChange.color};`;

        const selectedClass = this.state.selected ? 'selected' : '';

        const deleteButtonClassNames = classNames({
            'delete-button': true,
            'shown': this.props.isDeleteMode
        });

        return (
            <div className={`watchlist-item ${selectedClass}`} onClick={this.onClickSelect}>

                <div className={deleteButtonClassNames}>
                    {this.state.selected &&
                        <svg xmlns="http://www.w3.org/2000/svg" className="shown" viewBox="0 0 24 23">
                            <g fill="none" fill-rule="evenodd" transform="translate(.466)">
                                <circle cx="11.5" cy="11.5" r="11.264" fill="#0070F4" />
                                <path fill="#FFF" d="M9.556 15.1l-3.323-3.323a.783.783 0 0 1 1.108-1.107l2.774 2.774 5.544-5.543a.783.783 0 0 1 1.108 1.107L10.676 15.1a.78.78 0 0 1-.5.228h-.121a.78.78 0 0 1-.5-.227z" />
                            </g>
                        </svg>
                    }
                    {this.state.selected === false &&
                        <svg xmlns="http://www.w3.org/2000/svg" className={this.props.isDeleteMode ? 'shown' : ''} viewBox="0 0 24 23">
                            <g fill="none" fill-rule="evenodd" transform="translate(.466)">
                                <circle cx="11.5" cy="11.5" r="11.264" fill="#0070F4" />
                                <rect width="11.063" height="2" x="5.968" y="10.5" fill="#FFF" rx="1" />
                            </g>
                        </svg>
                    }

                </div>

                <img src={this.props.watchlistItem.imageUrl} alt="" />
                <div className="watchlist-item-content">
                    <p>{productName}</p>
                    <div className="prices">
                        {this.props.watchlistItem.newPrice &&
                            <>
                                <div className="new-price">{this.props.watchlistItem.newPrice}</div>
                                <div className="previous-price line-through">{this.props.watchlistItem.previousPrice}</div>
                            </>
                        }
                        {!this.props.watchlistItem.newPrice &&
                            <>
                                <div className="previous-price main">{this.props.watchlistItem.previousPrice}</div>
                            </>
                        }
                        <div className="pricedrop-value">
                            <PriceDropValue>
                                {this.props.watchlistItem.priceDropValue}
                            </PriceDropValue>
                        </div>
                    </div>
                </div>
            </div >
        );
    }
}