import React, { useEffect, useState } from "react";
import styled, { css, keyframes } from "styled-components";

export const FadeAnimation = (props) => {

    const [render, setRender] = useState(props.show);

    useEffect(() => {
        if (props.show) setRender(true);
    }, [props.show]);

    const onAnimationEnd = () => {
        if (props.onAnimationEnded) props.onAnimationEnded();
        if (!props.show) setRender(false);
    };

    const fadeInKeyFrame = keyframes`
        0% { opacity: 0; }
        100% { opacity: 1; }
    `;

    const fadeOutKeyFrame = keyframes`
        0% { opacity: 1; }
        100% { opacity: 0; }
    `;

    const fadeIn = css`
        ${fadeInKeyFrame} 0.3s;
    `;

    const fadeOut = css`
        ${fadeOutKeyFrame} 0.3s;
    `;

    const Container = styled.div`
        animation: ${propz => {
            if (propz.fade === 'fadeIn') return fadeIn;
            if (propz.fade === 'fadeOut') return fadeOut;
        }};
    `;

    return (
        render && (
            <Container fade={props.show ? 'fadeIn' : 'fadeOut'} onAnimationEnd={onAnimationEnd}>
                {props.children}
            </Container>
        )
    );
}