import React from "react";
import Lottie from "react-lottie";
import linkboxAnimation from '../../../../public/lotties/linkbox-animation.json';

import { BrowserService } from '../../../../../common/services/browser.service';
import { PopupMixpanelService } from "../../../../services/popup-mixpanel.service";

import './Linkbox.scss';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================

const browserService = new BrowserService();

const SHARE_FACEBOOK_COPY = 'Check out this free Chrome extension that gets you cashback when shopping online. I’ll also get $5 if you get cashback using it.';
const SHARE_TWITTER_COPY = 'Check out this free Chrome extension that gets you cashback when shopping online. I’ll also get $5 if you get cashback using it 😀';
const SHARE_EMAIL_SUBJECT_LINE = 'Thought you might like this';
const SHARE_EMAIL_COPY = 'Hey ___, I just started using this cool Chrome extension for getting cashback when shopping online. It lets you add items to a watchlist, recommends when to buy stuff, and it’s free. Use my link so I can get $5 if you get cashback using it!';

interface EarnyLinkBoxProps {
    referralLinkUrl: string;
}

interface EarnyLinkBoxState {
    copied: boolean;
}

const copyTextToClipBoard = (text: string) => {
    var input = document.createElement('input');
    input.setAttribute('value', text);
    document.body.appendChild(input);
    input.select();
    document.execCommand('copy');
    document.body.removeChild(input);
}

// ===========================================================================================
// --- EarnyLinkBox
// ===========================================================================================
export class EarnyLinkBox extends React.Component<EarnyLinkBoxProps, EarnyLinkBoxState> {

    constructor(props: EarnyLinkBoxProps) {
        super(props);

        this.state = {
            copied: false,
        };
    }

    copyEarnyLink = () => {
        if (this.state.copied === false) {
            copyTextToClipBoard(this.props.referralLinkUrl);
            this.setState({ copied: true });

            setTimeout(() => {
                this.setState({ copied: false });
            }, 2000);
        }
    }

    handleFacebookClick = () => {
        const text = encodeURIComponent(SHARE_FACEBOOK_COPY);
        const url = encodeURI(this.props.referralLinkUrl);
        const facebookSharingURL = `https://www.facebook.com/share.php?u=${url}&quote=${text}`;
        PopupMixpanelService.trackFriendReferralsShareViaPressed('Facebook');
        browserService.navigateToExternalUrl(facebookSharingURL, true);
    }

    handleTwitterClick = () => {
        const text = encodeURIComponent(SHARE_TWITTER_COPY);
        const url = encodeURI(this.props.referralLinkUrl);
        const twitterSharingURL = `https://twitter.com/intent/tweet?&text=${text}&url=${url}`;
        PopupMixpanelService.trackFriendReferralsShareViaPressed('Twitter');

        browserService.navigateToExternalUrl(twitterSharingURL, true);
    }

    handleMailClick = () => {
        const url = this.props.referralLinkUrl;
        const text = encodeURIComponent(SHARE_EMAIL_COPY.replace('**link**', url));
        const mailSharingUrl = `mailto:?to=&body=${text}&subject=${SHARE_EMAIL_SUBJECT_LINE}`;
        PopupMixpanelService.trackFriendReferralsShareViaPressed('Email');
        browserService.navigateToExternalUrl(mailSharingUrl, true);
    }

    render() {
        const animation = {
            loop: true,
            autoplay: true,
            animationData: linkboxAnimation,
            rendererSettings: {
                className: 'lottie',
                preserveAspectRatio: "xMidYMid slice"
            }
        };

        const personalLink = this.props.referralLinkUrl;
        const personalLinkTruncated = (personalLink.length > 27) ? (personalLink.substr(0, 27) + '...') : personalLink;

        return (
            <div id="earny-link-box" >

                <h3>How many friends do you have?</h3>

                <Lottie options={animation} height={122} />

                <div className="bottom">
                    <p className="friend-referral">
                        <b>Get <span style={{ color: '#19a388' }}>$5</span> for every friend </b>
                        you refer that makes a cashback purchase.&nbsp;
                        <a target="_blank" href=" https://support.earny.co/hc/en-us/articles/360000982033">
                            Learn more
                        </a>
                    </p>

                    <div className="copy-link">
                        <div className="link">{personalLinkTruncated}</div>
                        <button
                            disabled={this.state.copied}
                            className={'copy-button ' + (this.state.copied ? 'copied' : '')}
                            onClick={this.copyEarnyLink}>{this.state.copied ? 'Copied' : 'Copy'}</button>
                    </div>

                    <div className="social-media">
                        <button onClick={this.handleFacebookClick}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="21" height="21" viewBox="0 0 21 21">
                                <g fill="none" fill-rule="nonzero">
                                    <path fill="#4267B2" d="M1.596.5A1.104 1.104 0 0 0 .5 1.604v17.8A1.104 1.104 0 0 0 1.604 20.5h9.58v-7.734h-2.6V9.738h2.6V7.51c0-2.582 1.578-3.99 3.882-3.99.778-.003 1.555.037 2.328.12v2.7h-1.588c-1.254 0-1.498.6-1.498 1.47v1.928H17.3l-.4 3.028h-2.6V20.5h5.096c.61 0 1.104-.494 1.104-1.104v-17.8A1.104 1.104 0 0 0 19.396.5h-17.8z" />
                                    <path fill="#FFF" d="M14.3 20.5v-7.734h2.6l.4-3.028h-3V7.81c0-.874.244-1.47 1.498-1.47h1.588v-2.7a20.914 20.914 0 0 0-2.328-.12c-2.304 0-3.882 1.4-3.882 3.99v2.228h-2.6v3.028h2.6V20.5H14.3z" />
                                </g>
                            </svg>
                        </button>
                        <button onClick={this.handleTwitterClick}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="25" height="21" viewBox="0 0 25 21">
                                <path fill="#1DA1F2" fill-rule="nonzero" d="M7.973 20.5c9.289 0 14.369-7.695 14.369-14.368 0-.219 0-.437-.015-.653.988-.715 1.842-1.6 2.52-2.614-.922.408-1.9.676-2.9.794a5.067 5.067 0 0 0 2.22-2.793 10.12 10.12 0 0 1-3.207 1.226 5.055 5.055 0 0 0-8.606 4.606A14.337 14.337 0 0 1 1.946 1.422a5.054 5.054 0 0 0 1.564 6.74 5.012 5.012 0 0 1-2.292-.631v.064a5.052 5.052 0 0 0 4.05 4.95 5.042 5.042 0 0 1-2.28.087 5.056 5.056 0 0 0 4.719 3.506 10.133 10.133 0 0 1-7.474 2.094 14.297 14.297 0 0 0 7.74 2.264" />
                            </svg>
                        </button >
                        <button onClick={this.handleMailClick}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="29" height="21" viewBox="0 0 29 21">
                                <path fill="#FF6060" fill-rule="evenodd" d="M28.5 3.5v14a3 3 0 0 1-3 3h-22a3 3 0 0 1-3-3v-14c0-.503.124-.977.343-1.394l12.835 8.909a.92.92 0 0 0 .58.163.916.916 0 0 0 .581-.163l13.179-9.147c.305.47.482 1.03.482 1.632zm-3-3c.287 0 .565.04.828.116L14.259 8.99 2.351.728A2.991 2.991 0 0 1 3.5.5h22z" />
                            </svg>
                        </button >
                    </div >
                </div>
            </div>
        );
    }
}