import EY from '../EY';

// ===========================================================================================
// --- BrowserService
// ===========================================================================================
export class BrowserService {

    public static badgeColors = {
        blue: '#0070F4',
        green: '#19A388'
    }

    public getExtensionVersion() {
        if (!chrome || !chrome.runtime.getManifest) return 'v0.0.local';
        const version = chrome.runtime.getManifest().version;
        return version ? `v${version}` : '';
    }

    public async updateStorage(key: string, value: any): Promise<void> {
        chrome.storage.local.set({ [key]: value });
    }

    public async batchUpdateStorage(batch: any): Promise<void> {
        chrome.storage.local.set(batch);
    }

    public async removeFromLocalStorage(key: string): Promise<void> {
        chrome.storage.local.remove(key);
    }

    public async getStorageData<T>(key: string): Promise<T | null> {
        if (!chrome || !chrome.storage) throw new Error('Chrome API is unavailable');
        return new Promise((resolve, reject) => {
            chrome.storage.local.get(key, (data: any) => {
                if (data) {
                    if (data[key] === undefined) {
                        return resolve(null);
                    } else {
                        resolve(data[key]);
                    }
                }
                else reject(new Error(`The key ${key} was not found in localstorage`));
            });
        })
    }

    public async dumpStorageData(): Promise<any> {
        if (!chrome || !chrome.storage) throw new Error('Chrome API is unavailable');
        return new Promise((resolve, reject) => {
            chrome.storage.local.get(null, (data: any) => {
                if (data) {
                    resolve(data);
                }
                else reject(new Error(`Could not dump localstorage`));
            });
        })
    }

    public async setStorageData<T>(key: string, value: T): Promise<void> {
        if (!chrome || !chrome.storage) throw new Error('Chrome API is unavailable');
        return new Promise((resolve) => {
            chrome.storage.local.set({ [key]: value }, () => {
                resolve();
            });
        })
    }

    public decodeCookieBody(cookie: chrome.cookies.Cookie | null): any {
        if (!cookie || !cookie.value) return null;
        return JSON.parse(decodeURIComponent(cookie.value));
    }

    public encodeCookieBody(body: any): string {
        return encodeURIComponent(JSON.stringify(body));
    }

    public async getCookie(url: string, name: string): Promise<chrome.cookies.Cookie | null> {
        return new Promise((resolve) => {
            chrome.cookies.get({ url, name }, (cookie: chrome.cookies.Cookie | null) => {
                if (cookie) {
                    resolve(cookie);
                } else {
                    resolve(null);
                }
            });
        });
    }

    public async setCookie(url: string, name: string, domain: string, value: any, expirationDate?: number): Promise<boolean> {
        const cookie = {
            url,
            name,
            domain,
            value: this.encodeCookieBody(value),
            expirationDate
        };

        return new Promise((resolve) => {
            chrome.cookies.set(cookie, (cookie: chrome.cookies.Cookie | null) => {
                if (cookie) {
                    resolve(true);
                } else {
                    resolve(false);
                }
            });
        });
    }

    public async getAllTabs(): Promise<chrome.tabs.Tab[]> {
        return new Promise((resolve, reject) => {
            chrome.tabs.query({}, (tabs: chrome.tabs.Tab[]) => {
                if (tabs) resolve(tabs);
                else reject(new Error('Could not retrieve tabs'));
            });
        });
    }

    public async getCurrentActiveTab(): Promise<chrome.tabs.Tab> {
        return new Promise((resolve, reject) => {
            chrome.tabs.query({ active: true, currentWindow: true }, (tabs: chrome.tabs.Tab[]) => {
                const currentTab = tabs[0];
                if (currentTab) resolve(currentTab);
                else reject(new Error('Could not retrieve current Tab'));
            });
        });
    }

    public async getCurrentActiveTabId(): Promise<number> {
        const tab = await this.getCurrentActiveTab();
        if (!tab.id) throw new Error('Could not retrieve current Tab Id');
        return tab.id;
    }

    public async getCurrentActiveTabUrl(): Promise<string> {
        const tab = await this.getCurrentActiveTab();
        if (!tab.url) throw new Error('Could not retrieve current Tab url');
        return tab.url;
    }

    public async updateTab(tabId: any, newUrl: string): Promise<void> {
        if (!chrome || !chrome.tabs) throw new Error('Chrome API is unavailable');
        return new Promise((resolve) => {
            chrome.tabs.update(tabId, { url: newUrl }, () => {
                resolve();
            });
        });
    }

    public async updateTabAndWaitUntilPageIsLoaded(tabId: any, newUrl: string): Promise<void> {
        if (!chrome || !chrome.tabs) throw new Error('Chrome API is unavailable');
        return new Promise((resolve) => {
            chrome.tabs.update(tabId, { url: newUrl }, (tab?: chrome.tabs.Tab) => {
                chrome.tabs.onUpdated.addListener(function listener(tabId, info) {
                    if (info.status === 'complete' && tab && tabId === tab.id) {
                        chrome.tabs.onUpdated.removeListener(listener);
                        resolve();
                    }
                });
            });
        });
    }

    public navigateToExternalUrl(url: string, inNewTab = false, wait = false) {
        if (!chrome || !chrome.tabs) throw new Error('Chrome API is unavailable');

        if (!wait) {
            if (inNewTab) return chrome.tabs.create({ url });
            chrome.tabs.update({ url });
        } else {
            if (inNewTab) {
                return new Promise((resolve) => {
                    chrome.tabs.create({ url }, (createdTab?: chrome.tabs.Tab) => {
                        chrome.tabs.onUpdated.addListener(function listener(tabId, info) {
                            if (info.status === 'complete' && createdTab && createdTab.id === tabId) {
                                chrome.tabs.onUpdated.removeListener(listener);
                                resolve();
                            }
                        });
                    });
                });
            } else {
                return new Promise((resolve) => {
                    chrome.tabs.update({ url }, (updatedTab?: chrome.tabs.Tab) => {
                        chrome.tabs.onUpdated.addListener(function listener(tabId, info) {
                            if (info.status === 'complete' && updatedTab && updatedTab.id === tabId) {
                                chrome.tabs.onUpdated.removeListener(listener);
                                resolve();
                            }
                        });
                    });
                });
            }
        }
    }

    public async getSharingStatus(): Promise<boolean> {
        return new Promise((resolve) => {
            chrome.storage.local.get("sharingStatus", (data: any) => {
                if (data && data.sharingStatus) resolve(data.sharingStatus);
                else resolve(false);
            });
        });
    }

    public async setSharingStatus(isEnabled: boolean): Promise<void> {
        return new Promise((resolve) => {
            chrome.storage.local.set({ sharingStatus: isEnabled }, () => {
                resolve();
            });
        });
    }

    public static clearLocalStorage() {
        chrome.storage.local.clear(() => { });
    }

    public async setBadge(text: string, tabId: number): Promise<void> {
        await chrome.browserAction.setBadgeText({ text, tabId });
    }

    public async setBrowserNotificationBanner(options: chrome.notifications.NotificationOptions) {
        chrome.notifications.create('', options);
    }

    public async setBadgeBackgroundColor(colorHex: string): Promise<void> {
        await chrome.browserAction.setBadgeBackgroundColor({ color: colorHex });
    }

    public async setGreyEarnyIcon() {
        await chrome.browserAction.setIcon({ path: 'icons/icon-grey.png' });
    }

    public async setYellowEarnyIcon() {
        await chrome.browserAction.setIcon({ path: 'icons/logo-80.png' });
    }

    public async requestMinimizeEarnyWidget() {
        const currentTab = await this.getCurrentActiveTab();
        return new Promise((resolve, reject) => {
            if (!currentTab || !currentTab.id) {
                return reject(new Error('Unable to retrieve current tab'));
            }
            chrome.tabs.sendMessage(currentTab.id, { type: 'minimizeEarnyWidget' }, () => {
                if (chrome.runtime.lastError) {
                    reject(chrome.runtime.lastError);
                };
                resolve();
            });
        });
    }

    public async getStartupModalShown(): Promise<boolean> {
        const shown: boolean | null = await this.getStorageData<boolean>(EY.StorageKeys.STORAGE_KEY_STARTUP_MODAL_SHOWN);
        if (!shown) return false;
        return shown;
    }

    public async setStartupModalShown(): Promise<void> {
        return this.setStorageData(EY.StorageKeys.STORAGE_KEY_STARTUP_MODAL_SHOWN, true);
    }

    public setUninstallUrl(userId?: string) {
        const version = this.getExtensionVersion();
        if (userId) {
            chrome.runtime.setUninstallURL(`${EY.Urls.EARNY_UNINSTALL_URL}?userId=${userId}&version=${version}`);
        } else {
            chrome.runtime.setUninstallURL(`${EY.Urls.EARNY_UNINSTALL_URL}?version=${version}`);
        }
    }
}
