import React from "react";
import { PopupMixpanelService } from '../../../../services/popup-mixpanel.service';
import { BrowserService } from "../../../../../common/services/browser.service";

import './WatchListConfirmationModal.scss';
import EY from "../../../../../common/EY";

const browserService = new BrowserService();

export default class WatchListConfirmationModal extends React.Component<any, any> {

    constructor(props: any) {
        super(props);
        this.state = {
            show: false
        }
    }

    handleAppStoreClick = () => {
        PopupMixpanelService.trackiOSAppStoreButtonPressed();
        browserService.navigateToExternalUrl(EY.Urls.EARNY_APP_IOS_STORE_URL, true);
    }

    handleAndroidStoreClick = () => {
        PopupMixpanelService.trackAndroidAppStoreButtonPressed();
        browserService.navigateToExternalUrl(EY.Urls.EARNY_APP_ANDROID_STORE_URL, true);
    }

    show = () => {
        this.setState({ show: true });
    }

    close = () => {
        this.setState({ show: false });
    }

    render() {
        return (
            <>
                <div id="confirmation-modal" className={this.state.show ? 'show fadeIn' : 'hidden fadeOut'}>
                    <div className="modal-content">
                        <button id="close" onClick={this.close}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 15 15">
                                <g fill="none" fill-rule="evenodd" stroke="#232E3B" stroke-linecap="round" stroke-width="2.36">
                                    <path d="M2.289 1.3L13 13M12.711 1.3L2 13" />
                                </g>
                            </svg>
                        </button>
                        <div className="modal-price-drop">
                            <svg xmlns="http://www.w3.org/2000/svg" width="69" height="70" viewBox="0 0 69 70">
                                <g fill="none" fill-rule="evenodd" transform="translate(-.5)">
                                    <circle cx="35" cy="35" r="34.5" fill="#BFE8E0" />
                                    <circle cx="35" cy="35" r="26.172" fill="#63CBB7" />
                                    <path fill="#FFF" stroke="#FFF" d="M48.45 27.541a1.848 1.848 0 0 1 0 2.614L34.076 44.529a1.842 1.842 0 0 1-1.311.541 1.837 1.837 0 0 1-1.332-.54l-7.841-7.84a1.848 1.848 0 1 1 2.614-2.614l6.547 6.548 13.083-13.083a1.848 1.848 0 0 1 2.614 0z" />
                                </g>
                            </svg>
                            <div className="price-drop-notice">
                                <p>We’ll let you know if there’s a price drop. View your watchlist by downloading our app.</p>
                            </div>
                        </div>
                        <div className="download-links">
                            <button id="app-store-button" className="store-button" onClick={this.handleAppStoreClick}></button>
                            <button id="android-store-button" className="store-button" onClick={this.handleAndroidStoreClick}></button>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}