import React from "react";
import styled from 'styled-components';

import './GradientStaticSlider.scss';

interface GradientStaticSliderProps {
    percentage: number;
}
const colorFrom = {
    red: 255,
    green: 220,
    blue: 0
}
const colorTo = {
    red: 58,
    green: 224,
    blue: 192
}

export class GradientStaticSlider extends React.Component<GradientStaticSliderProps, any> {

    render() {

        const percentZeroToOne = this.props.percentage;
        const thumbRed = colorFrom.red + percentZeroToOne * (colorTo.red - colorFrom.red);
        const thumbGreen = colorFrom.green + percentZeroToOne * (colorTo.green - colorFrom.green);
        const thumbBlue = colorFrom.blue + percentZeroToOne * (colorTo.blue - colorFrom.blue);

        const Input = styled.input`
            &::-webkit-slider-thumb {
                background-color: rgba(${thumbRed}, ${thumbGreen}, ${thumbBlue}, 1);
                filter: brightness(90%);

            }
        `;
        return (
            <div id="gradientStaticSlider">
                <Input type="range" min="0" max="1" step="0.1" value={this.props.percentage} className="range blue"></Input>
            </div>
        )
    }
}