import React from "react";
import EY from "../../../../../common/EY";
import { PopupMixpanelService } from "../../../../services/popup-mixpanel.service";
import { BrowserService } from "../../../../../common/services/browser.service";
import { EarnyPlusText } from "../../../../../common/react/EarnyPlusTest/EarnyPlusText";

import './SettingScreenLinks.scss';

const browserService = new BrowserService();

const SettingsScreenLink: React.FC<any> = (props: any) => {
    return (
        <button className="settings-screen-link" onClick={props.onClick}>
            {props.children}
        </button>
    )
}

export class SettingScreenLinks extends React.Component<{ isSubscribed: boolean }, any> {

    handleHelpClick = async () => {
        PopupMixpanelService.trackHelpButtonPressed();
        browserService.navigateToExternalUrl(EY.Urls.EARNY_SUPPORT_URL, true);
    }

    handleSettingsClick = async () => {
        PopupMixpanelService.trackSettingsButtonPressed();
        browserService.navigateToExternalUrl(EY.Urls.EARNY_SETTINGS_PAGE_URL, true);
    }

    handleEarnyPlusClick = async () => {
        PopupMixpanelService.trackEarnyPlusButtonPressed();

        if (this.props.isSubscribed) {
            browserService.navigateToExternalUrl(EY.Urls.EARNY_ACCOUNT_PLAN_URL, true);
        } else {
            browserService.navigateToExternalUrl(EY.Urls.EARNY_PLAN_URL, true);
        }
    }

    render() {
        return (
            <div className="setting-screen-links">
                <SettingsScreenLink onClick={this.handleEarnyPlusClick}>
                    <EarnyPlusText fontSize={16} fontWeight={600} />
                </SettingsScreenLink>

                <SettingsScreenLink onClick={this.handleHelpClick}>
                    FAQs
                </SettingsScreenLink>

                <SettingsScreenLink onClick={this.handleSettingsClick}>
                    Settings
                </SettingsScreenLink>
            </div>
        );
    }
}