import React from "react";
import { AccountBalance } from "../../../../../common/interfaces";
import { Profile } from '../../../../../common/interfaces/profile';

import './EarningsSummary.scss';

export class EarningsSummary extends React.Component<{ profile: Profile }, any> {

    render() {
        const accountBalance: AccountBalance | undefined = this.props.profile.accountBalance;
        const lifetimeBalance: string = accountBalance ? accountBalance.lifetimeBalance.value : '$0';
        const currentBalance: string = accountBalance ? accountBalance.currentBalance.value : '$0';
        const pendingBalance: string = accountBalance ? accountBalance.pendingBalance.value : '$0';

        return (
            <div id="earnings">
                <div className="earning">
                    <img src="images/profile/current-icon.svg" alt="Current" />
                    <h3>CURRENT</h3>
                    <h2>{currentBalance}</h2>
                </div>
                <div className="earning">
                    <img src="images/profile/pending-icon.svg" alt="Pending" />
                    <h3>PENDING</h3>
                    <h2>{pendingBalance}</h2>
                </div>
                <div className="earning">
                    <img src="images/profile/lifetime-icon.svg" alt="Lifetime" />
                    <h3>LIFETIME</h3>
                    <h2>{lifetimeBalance}</h2>
                </div>
            </div>
        );
    }
}