import React from "react";
import SyncLoader from "react-spinners/SyncLoader";
import EY from "../../../../common/EY";

import { PopupScreen } from "../../PopupScreen";
import { PopupMixpanelService } from '../../../services/popup-mixpanel.service';
import { BrowserService } from "../../../../common/services/browser.service";
import { MessageSender } from '../../../../common/messaging/message-sender';
import { User } from "../../../../common/interfaces";
import { EarnyLinkBox } from "./Linkbox/Linkbox";
import { RouteComponentProps } from "react-router-dom";
import { Profile } from "../../../../common/interfaces/profile";
import { EarnyPlusCard } from "./ProfileCards/Cards/EarnyPlusCard";
import { FreeSubscriptionCard } from "./ProfileCards/Cards/FreeSubscriptionCard";
import { EarningsSummary } from './Earnings/EarningsSummary';
import { SettingScreenLinks } from './Links/SettingScreenLinks';
import { CashoutComponent } from "./Cashout/CashoutComponent";

import './ProfileScreen.scss';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================

const browserService = new BrowserService();

const version = browserService.getExtensionVersion();
interface ProfileScreenProps extends RouteComponentProps<any> {

}

interface ProfileScreenState {
  loaded: boolean;
  user?: User;
  profile?: Profile;
  canCashout: boolean;
  isSubscribed: boolean;
  showActivateFreeMonth: boolean;
  showActivateEarnyPlus: boolean;
  showFreeMonthActivatedConfirmation: boolean;
}

// ===========================================================================================
// --- ProfileScreen
// ===========================================================================================
export class ProfileScreen extends PopupScreen<ProfileScreenProps, ProfileScreenState> {

  constructor(props: ProfileScreenProps) {
    super(props, 'profile');

    this.state = {
      loaded: false,
      canCashout: false,
      isSubscribed: false,
      showActivateFreeMonth: false,
      showActivateEarnyPlus: false,
      showFreeMonthActivatedConfirmation: false
    }
  }

  async componentDidMount() {
    super.componentDidMount();

    try {

      const [user, profile] = await Promise.all([
        MessageSender.getUser(),
        MessageSender.getProfile()
      ])

      if (!user || !profile) return;


      console.log('user', user);

      const userHadASubscription: boolean = user.userData !== undefined && user.userData.user.status_map.subscription.has_subscribed;
      const isSubscribed: boolean = user.userData !== undefined && user.userData.user.status_map.subscription.linked;
      const showActivateFreeMonth: boolean = !userHadASubscription;
      const showActivateEarnyPlus: boolean = !showActivateFreeMonth && !isSubscribed;

      const newState = {
        user,
        canCashout: false,
        isSubscribed,
        loaded: true,
        profile,
        showActivateFreeMonth,
        showActivateEarnyPlus
      };

      if (profile && profile.accountBalance) {
        newState.canCashout = profile.accountBalance.cashout.allowed;
      }

      this.setState(newState);
    } catch (error) {

    }
  }

  handleAppStoreClick = async () => {
    PopupMixpanelService.trackiOSAppStoreButtonPressed();
    browserService.navigateToExternalUrl(EY.Urls.EARNY_APP_IOS_STORE_URL, true);
  }

  handleAndroidStoreClick = async () => {
    PopupMixpanelService.trackAndroidAppStoreButtonPressed();
    browserService.navigateToExternalUrl(EY.Urls.EARNY_APP_ANDROID_STORE_URL, true);
  }

  // handleFriendReferralEarningsClick = async () => {
  //   this.props.history.push('/profile/friend-referral-earnings', {});
  // }


  renderSettingsScreen = () => {
    const profile: Profile = this.state.profile!;

    return (
      <div id="profile-canvas">

        {this.state.user &&
          <h3 className="user-name">
            {this.state.user.firstName} {this.state.user.lastName}
          </h3>
        }

        <EarningsSummary profile={profile} />

        <CashoutComponent profile={profile} canCashout={this.state.canCashout} />

        {profile.friends &&
          <EarnyLinkBox referralLinkUrl={profile.friends.referral_link} />
        }

        {this.state.showActivateEarnyPlus &&
          <EarnyPlusCard />
        }

        <FreeSubscriptionCard enabled={this.state.showActivateFreeMonth} />

        <SettingScreenLinks isSubscribed={this.state.isSubscribed}/>

        <div className="download-links">
          <button id="app-store-button" className="store-button" onClick={this.handleAppStoreClick}></button>
          <button id="android-store-button" className="store-button" onClick={this.handleAndroidStoreClick}></button>
        </div>

        <div className="settings-app-version">
          <p>{version}</p>
        </div>
      </div>
    );
  }

  render() {
    if (this.state.loaded === false) {
      return (
        <div id="earny-loader">
          <SyncLoader
            size={17}
            color={"#71dcc1"}
            loading={!this.state.loaded}
          />
        </div>
      )
    }
    return this.renderSettingsScreen();
  }
}
