import React, { RefObject } from "react";
import Lottie from "react-lottie";
import SyncLoader from "react-spinners/SyncLoader";
import WatchListConfirmationModal from "./modal/WatchListConfirmationModal";
import { PriceHistoryChart } from "./price-history-chart/PriceHistoryChart";
import { PopupMixpanelService } from "../../../services/popup-mixpanel.service";
import { ProductDetails, Product, PriceDetails } from "../../../../common/interfaces";
import { MessageSender } from "../../../../common/messaging/message-sender";
import { GradientStaticSlider } from "../../../../common/react/GradientStaticSlider/GradientStaticSlider";
import { ProductState, ProductStateParser } from "../../../../common/product-state-parser/product-state-parser";
import { RouteComponentProps } from 'react-router-dom';
import { PopupScreen } from "../../PopupScreen";
import pdpAnimation from '../../../public/lotties/loading-pdp-animation.json';

import './ProductDetailsScreen.scss';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================
interface ProductDetailsScreenProps extends RouteComponentProps<any> {

}

interface ProductDetailsScreenState {
    loaded: boolean;
    product: Product | null;
    isProductSavedToWatchlist: boolean;
    showProductDetailsPending: boolean;
    isProductFromWatchlist: boolean;
}

// ===========================================================================================
// --- PriceTrackingScreen
// ===========================================================================================
export class ProductDetailsScreen extends PopupScreen<ProductDetailsScreenProps, ProductDetailsScreenState>{

    private modal: RefObject<WatchListConfirmationModal> = React.createRef();

    constructor(props: ProductDetailsScreenProps) {
        super(props, 'product-details');

        this.state = {
            loaded: false,
            product: null,
            isProductSavedToWatchlist: false,
            showProductDetailsPending: false,
            isProductFromWatchlist: false
        };
    }

    async componentDidMount() {
        super.componentDidMount();

        let product: Product | null = null;
        let isProductSavedToWatchlist: boolean = false;
        let isProductFromWatchlist: boolean = false;

        if (this.props.match.params.itemId) {
            product = await MessageSender.getWatchlistItem(this.props.match.params.itemId);
            isProductFromWatchlist = true;

        } else {
            product = await MessageSender.getActiveProduct();
        }

        try {

            if (product && product.productDetails) {
                if (product.productDetails.isInWatchlist) {
                    isProductSavedToWatchlist = product.productDetails.isInWatchlist;
                }
            }

            this.setState({
                product,
                isProductSavedToWatchlist,
                isProductFromWatchlist,
                loaded: true
            });

        } catch (error) {
            console.log('Error', error);
        }
    }

    onClickPrevious = () => {
        this.props.history.goBack();
    }

    onSave = async () => {
        const isFromProductDetails: boolean = true;
        const isProductSavedToWatchlist = !this.state.isProductSavedToWatchlist;
        this.setState({ isProductSavedToWatchlist });

        if (isProductSavedToWatchlist) {
            PopupMixpanelService.trackProductDetailsAddToWatchlistCTAPressed();
            await MessageSender.addItemToWatchlist(isFromProductDetails);
            if (this.modal.current) {
                this.modal.current.show();
            }
        } else {
            PopupMixpanelService.trackProductDetailsAddToWatchlistCTAPressed();
            await MessageSender.removeItemFromWatchlist(isFromProductDetails);
            if (this.modal.current) {
                this.modal.current.close();
            }
        }
    }

    addProductToWatchlistForPending = async () => {
        PopupMixpanelService.trackProductDetailsAddToWatchlistCTAPressed();
        if (await MessageSender.addItemToWatchlistPendingProductDetails() === true) {
            this.setState({ showProductDetailsPending: true });
        }
    }

    renderNoProductDetails = () => {
        return (
            <div id="product-details-canvas">
                <div className="no-product-details">
                    <p className="add-to-watchlist-title">Add this product to your watchlist to view details</p>

                    <p className="watchlist-information-title">Information will be updated within the hour</p>

                    <button id="add-to-watchlist" onClick={this.addProductToWatchlistForPending}>
                        Add to Watchlist
                    </button>
                </div>
            </div>
        )
    }

    renderProductDetailsPending = () => {
        const animation = {
            loop: true,
            autoplay: true,
            animationData: pdpAnimation,
            rendererSettings: {
                className: 'lottie',
                preserveAspectRatio: "xMidYMid slice"
            }
        };

        return (
            <div id="product-details-canvas">
                <div className="product-details-pending">

                    <Lottie options={animation} height={150} width={150} />

                    <p className="loading-product-details-title">Loading product details...</p>

                    <p className="information-title">Information will be updated within the hour</p>
                </div>
            </div>
        )
    }

    renderIsNotProductPage = () => {
        return (
            <div id="product-details-canvas">
                <div className="price-tracking-earny-logo">
                    <img src="images/earny-logo.svg" alt="earny-logo" />
                </div>
                <div className="pdp-no-product">
                    <img src="images/product-details/no-pdp.svg" alt="no-product-details-icon" width="100" />
                </div>
                <div className="pdp-no-product-description">
                    <p>Visit a product page for more details and to add to your watchlist</p>
                </div>
            </div>
        )
    }

    renderPriceProtected(productDetails: ProductDetails, priceDetails?: PriceDetails) {
        let productName: string = productDetails.name || '';
        productName = (productName.length >= 70) ? (productName.substring(0, 70) + '...') : productName;
        const lowestPriceTitle: string = (priceDetails && priceDetails.lowestPrice) ? priceDetails.lowestPrice.title : 'LOWEST';
        const highestPriceTitle: string = (priceDetails && priceDetails.highestPrice) ? priceDetails.highestPrice.title : 'HIGHEST';
        let lowestPriceValue: string = (priceDetails && priceDetails.lowestPrice) ? priceDetails.lowestPrice.value.toString() : 'N/A';
        let highestPriceValue: string = (priceDetails && priceDetails.highestPrice) ? priceDetails.highestPrice.value.toString() : 'N/A';

        if (lowestPriceValue[0] !== '$') lowestPriceValue = '$' + lowestPriceValue;
        if (highestPriceValue[0] !== '$') highestPriceValue = '$' + highestPriceValue;

        return (
            <div id="product-details-canvas">

                {this.state.isProductFromWatchlist &&
                    <div id="previous-button" onClick={this.onClickPrevious}>
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 240.823 240.823">
                            <path d="M57.633 129.007L165.93 237.268c4.752 4.74 12.451 4.74 17.215 0 4.752-4.74 4.752-12.439 0-17.179l-99.707-99.671 99.695-99.671c4.752-4.74 4.752-12.439 0-17.191-4.752-4.74-12.463-4.74-17.215 0L57.621 111.816c-4.679 4.691-4.679 12.511.012 17.191z" />
                        </svg>
                    </div>
                }

                <div className="product-details">
                    <img src={productDetails.imageUrl} alt="" />
                    <div className="product-content">
                        <div className="product-name">
                            <p>{productName}</p>
                        </div>
                        <div className="product-prices">
                            {productDetails.price &&
                                <div className="product-new-price">
                                    {productDetails.price}
                                </div>
                            }
                            <button id="save-button" className={this.state.isProductSavedToWatchlist ? 'saved' : 'unsaved'} onClick={this.onSave}>
                                <svg xmlns="http://www.w3.org/2000/svg" width="23" height="20" viewBox="0 0 26 22">
                                    <path id="save-path" fill="none" fill-rule="evenodd" stroke="#232E3B" stroke-width="2" d="M20.407 1.487c-4.558-2.013-7.783 2.888-7.783 2.888S9.399-.525 4.84 1.484c-3.565 1.575-4.879 5.97-2.957 9.36 1.48 2.615 4.512 5.908 10.21 9.986a.915.915 0 0 0 1.062 0c5.695-4.074 8.727-7.371 10.208-9.982 1.92-3.391.604-7.786-2.957-9.361z" />
                                </svg>
                            </button>
                        </div>
                    </div>
                </div>

                {priceDetails && priceDetails.timeToBuy &&
                    <div className="product-time-to-buy">
                        <span className="left">Wait</span>
                        <GradientStaticSlider percentage={priceDetails.timeToBuy.value} />
                        <span className="right">Buy</span>
                    </div>
                }

                <div id="price-stats">
                    <div className="price-stat left">
                        <p>{highestPriceTitle}</p>
                        <p className="price">{highestPriceValue}</p>
                    </div>

                    <div className="price-stat right">
                        <p>{lowestPriceTitle}</p>
                        <p className="price">{lowestPriceValue}</p>
                    </div>
                </div>

                <PriceHistoryChart chartData={priceDetails ? priceDetails.chartData : undefined} />

                <WatchListConfirmationModal ref={this.modal} />
            </div>
        );
    }

    render() {

        if (this.state.loaded === false) {
            return (
                <div id="earny-loader">
                    <SyncLoader
                        size={17}
                        color={"#71dcc1"}
                        loading={!this.state.loaded}
                    />
                </div>
            )
        }

        if (this.state.showProductDetailsPending) {
            return this.renderProductDetailsPending();
        }

        const product: Product | null = this.state.product;
        const productState: ProductState = ProductStateParser.getState(product);

        switch (productState) {
            case ProductState.NO_PRODUCT_DETAILS:
                return this.renderNoProductDetails();

            case ProductState.NO_PRODUCT_PAGE:
                return this.renderIsNotProductPage();

            case ProductState.PRODUCT_DETAILS_PENDING:
                return this.renderProductDetailsPending();

            case ProductState.PRODUCT_DETAILS_VALID:
                return this.renderPriceProtected(product!.productDetails!, product!.priceDetails);

            default:
                return this.renderIsNotProductPage();
        }
    }
}