
import React from 'react';
import Lottie from 'react-lottie';

import './ProfileCard.scss';

interface ProfileCardProps {
    children?: any;
    onCTAClicked: () => void;
    bannerImg: string;
    ctaText: string;
    lottieAnimationData?: any;
}

export const SubscriptionCard: React.FC<ProfileCardProps> = (props: ProfileCardProps) => {
    let animation;

    if (props.lottieAnimationData) {
        animation = {
            loop: true,
            autoplay: true,
            animationData: props.lottieAnimationData,
            rendererSettings: {
                className: 'lottie',
                preserveAspectRatio: "xMidYMid slice"
            }
        };
    }

    return (
        <div className="profile-card">

            <div className={animation ? 'banner with-animation' : 'banner'} >
                <img src={props.bannerImg} alt="" />
                {animation &&
                    <div className="lottie">
                        <Lottie options={animation} isClickToPauseDisabled={true} />
                    </div>
                }
            </div>

            <div className="card-content">
                {props.children}
                <button className="cta-button" onClick={props.onCTAClicked}>
                    {props.ctaText}
                </button>
            </div>
        </div>
    );
}