import { Retailer } from "../interfaces";

export interface MixpanelFormattedRetailer {
    cashbackRate: string;
    cashbackUrlTemplate: string;
    isCashbackActivated: boolean;
    retailerName: string;
    retailerId: string;
}

export class Formatters {
    public static formatRetailerForMixpanel(retailer: Retailer): MixpanelFormattedRetailer {
        return {
            cashbackRate: retailer.cashbackRate,
            cashbackUrlTemplate: retailer.cashbackUrlTemplate,
            isCashbackActivated: retailer.isCashbackActivated || false,
            retailerName: retailer.name,
            retailerId: retailer.retailerId.toString()
        }
    }
}
