import * as _ from 'lodash';
import React from "react";
import Lottie from "react-lottie";
import SyncLoader from "react-spinners/SyncLoader";
import { RouteComponentProps } from "react-router-dom";
import { WatchlistItem } from "../../../../common/interfaces";
import { MessageSender } from '../../../../common/messaging/message-sender';
import { PopupMixpanelService } from '../../../services/popup-mixpanel.service';
import { WatchlistItemComponent } from './WatchlistItemComponent/WatchlistItemComponent';
import { PopupScreen } from '../../PopupScreen';

import watchlistAnimation from "../../../public/lotties/add-to-watchlist-animation.json";

import './WatchlistScreen.scss';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================
const classNames = require('classnames');

interface WatchlistScreenProps extends RouteComponentProps<any> {

}

interface WatchlistScreenState {
    loaded: boolean;
    isDeleteMode: boolean;
    watchlist: WatchlistItem[];
    selectedItems: WatchlistItem[];
}

function WatchlistStep(props: any) {
    return (
        <div className="watchlist-step-inline">
            <div className="watchlist-step-number"><span>{props.number}</span></div>
            <div className="watchlist-step-description">
                <p>{props.text}</p>
            </div>
        </div>
    );
}

// ===========================================================================================
// --- WatchlistScreen
// ===========================================================================================
export class WatchlistScreen extends PopupScreen<WatchlistScreenProps, WatchlistScreenState> {

    constructor(props: WatchlistScreenProps) {
        super(props, 'watchlist');

        this.state = {
            loaded: false,
            isDeleteMode: false,
            watchlist: [],
            selectedItems: []
        }
    }

    async componentDidMount() {
        super.componentDidMount();
        const watchlist = await MessageSender.getWatchlist();
        this.setState({ watchlist, loaded: true });
        PopupMixpanelService.trackWatchlistPresented();
    }

    onClickedItem = (clickedItem: WatchlistItem) => {
        this.props.history.push(`/watchlist/details/${clickedItem.itemId}`, { itemId: clickedItem.itemId });
    }

    onSelectedItem = async (selectedItem: WatchlistItem) => {
        const items = this.state.selectedItems;
        items.push(selectedItem);
        this.setState({ selectedItems: items });
        PopupMixpanelService.trackWatchlistItemSelected();
    }

    onUnselectedItem = (unselectedItem: WatchlistItem) => {
        let items = this.state.selectedItems;
        items = _.filter(items, (item: WatchlistItem) => {
            return item.itemId !== unselectedItem.itemId;
        });
        this.setState({ selectedItems: items });
    }

    startDeletion = async () => {
        this.setState({ isDeleteMode: true });
        PopupMixpanelService.trackWatchlistEditItemsButtonPressed();
    }

    cancelDeletion = () => {
        this.setState({ isDeleteMode: false, selectedItems: [] });
    }

    removeItems = async () => {

        const itemIds: string[] = this.state.selectedItems.map((item: WatchlistItem) => item.itemId);
        await MessageSender.removeItemBatchFromWatchlist(itemIds);
        const watchlist = _.filter(this.state.watchlist, (item: WatchlistItem) => {
            return !itemIds.includes(item.itemId);
        });
        this.setState({ watchlist, isDeleteMode: false });
        PopupMixpanelService.trackWatchlistItemsRemoveButtonPressed();
    }

    renderList = () => {
        const watchlistItems: any = [];

        this.state.watchlist
            .filter((item: WatchlistItem) => {
                return (item.name !== undefined && item.name !== null && item.name !== '');
            })
            .map((item: WatchlistItem) => {
                if (item.name) {
                    return watchlistItems.push(
                        <WatchlistItemComponent
                            isDeleteMode={this.state.isDeleteMode}
                            watchlistItem={item}
                            onItemClicked={this.onClickedItem}
                            onSelected={this.onSelectedItem}
                            onUnselected={this.onUnselectedItem} />
                    );
                }
            });

        const removeButtonClasses = classNames({
            'shown': this.state.isDeleteMode,
            'enabled': (this.state.selectedItems.length > 0)
        });


        return (
            <div id="watchlist-canvas">
                <div id="list" className={this.state.isDeleteMode ? 'delete-mode' : ''}>
                    {watchlistItems}
                </div>

                <button id="edit-button" className={this.state.isDeleteMode ? 'cancel' : ''} onClick={this.state.isDeleteMode ? this.cancelDeletion : this.startDeletion}>
                    {this.state.isDeleteMode === false &&
                        <svg xmlns="http://www.w3.org/2000/svg" width="17" height="16" viewBox="0 0 17 16">
                            <path fill="#232E3B" fill-rule="nonzero" stroke="#232E3B" stroke-width=".5" d="M15.504 2.352l-.988-.988a2.098 2.098 0 0 0-2.964 0l-8.893 8.892a.465.465 0 0 0-.118.202l-1.318 4.61a.467.467 0 0 0 .576.578l4.611-1.318a.46.46 0 0 0 .202-.118l8.892-8.894c.396-.396.614-.922.614-1.482 0-.56-.218-1.087-.614-1.482zM6.038 13.464L2.35 14.518l1.053-3.69 7.16-7.16 2.635 2.635-7.16 7.161zm8.808-8.806l-.988.988-2.636-2.636.988-.988a1.167 1.167 0 0 1 1.648 0l.988.988c.22.22.34.512.34.824 0 .31-.12.604-.34.824z" />
                        </svg>
                    }
                    {this.state.isDeleteMode === true &&
                        <svg xmlns="http://www.w3.org/2000/svg" width="14" height="15" viewBox="0 0 14 15">
                            <g fill="none" fill-rule="evenodd" stroke="#232E3B" stroke-linecap="round" stroke-width="2.36">
                                <path d="M1.789 1.3L12.5 13M12.211 1.3L1.5 13" />
                            </g>
                        </svg>
                    }
                </button>

                <button
                    id="remove-button"
                    disabled={(this.state.selectedItems.length === 0)}
                    className={removeButtonClasses}
                    onClick={this.removeItems}>
                    Remove
                </button>

            </div >
        );
    }

    renderListEmpty = () => {
        const animation = {
            loop: true,
            autoplay: true,
            animationData: watchlistAnimation,
            rendererSettings: {
                preserveAspectRatio: "xMidYMid slice"
            }
        };

        return (
            <div id="watchlist-canvas" className="empty">
                <div className="animation">
                    <Lottie options={animation} height={201} width={312} />
                </div>
                <p>Start adding to your watchlist</p>
                <div className="add-to-watchlist-steps">
                    <WatchlistStep number="1" text="Visit a retailer product page" />
                    <WatchlistStep number="2" text="Click on the Earny widget to add to your watchlist, view price history, and more" />
                </div>
            </div >
        );
    }

    render() {

        if (this.state.loaded === false) {
            return (
                <div id="earny-loader">
                    <SyncLoader
                        size={17}
                        color={"#71dcc1"}
                        loading={!this.state.loaded}
                    />
                </div>
            )
        }

        if (this.state.watchlist.length === 0) {
            return this.renderListEmpty();
        }
        return this.renderList();
    }
}