import React from 'react';

import { LoginScreen } from './components/screens/Login/LoginScreen';
import { Main } from './components/screens/Main/Main';
import { PopupMixpanelService } from './services/popup-mixpanel.service';
import { BrowserService } from '../common/services/browser.service';
import { MessageSender } from '../common/messaging/message-sender';

import './EarnyPopup.scss';

const browserService = new BrowserService();

interface EarnyPopupState {
  loaded: boolean;
  isLoggedIn?: boolean;
}

export class EarnyPopup extends React.Component<{}, EarnyPopupState> {

  constructor(props: any) {
    super(props);
    this.state = {
      loaded: false
    }
  }

  async componentDidMount() {

    await MessageSender.requestBackgroundSync();

    PopupMixpanelService.trackChromeExtensionPopupOpened();

    const isLoggedIn: boolean = await MessageSender.isUserLoggedIn();
    this.setState({ isLoggedIn, loaded: true });

    try {
      await browserService.requestMinimizeEarnyWidget();
    } catch (error) {
      // console.error(error.message);
    }
  }

  render() {
    return (
      <div className="Earny">

        {this.state.loaded === false &&
          <div id="boot-container"></div>
        }

        {this.state.isLoggedIn === true &&
          <Main />
        }

        {this.state.isLoggedIn === false &&
          <LoginScreen />
        }

      </div>
    );
  }
}