import React from "react";
import Lottie from "react-lottie";

import { SwitchComponent } from "../../../../common/react/SwitchComponent/SwitchComponent";
import wildlinkAnimation from "../../../public/lotties/wildlink-animation-mobile.json";
import { MessageSender } from "../../../../common/messaging/message-sender";
import { BrowserService } from "../../../../common/services/browser.service";
import { PopupScreen } from "../../PopupScreen";

import './ShareScreen.scss';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================
interface ShareScreenProps {

}

interface ShareScreenState {
  isSharingEnabled: boolean
}

const browserService = new BrowserService();

// ===========================================================================================
// --- ShareScreen
// ===========================================================================================
function ShareDetail(props: any) {
  return (
    <div className="share-detail-inline">
      <div className="share-detail-number"><span>{props.number}</span></div>
      <div className="share-detail-description">
        <p>{props.text}</p>
      </div>
    </div>
  );
}

export class ShareScreen extends PopupScreen<ShareScreenProps, ShareScreenState>{

  constructor(props: ShareScreenProps) {
    super(props, 'share');

    this.state = {
      isSharingEnabled: false
    };
  }

  async componentDidMount() {
    super.componentDidMount();

    let isSharingEnabled: boolean = await browserService.getSharingStatus();
    this.setState({ isSharingEnabled });
  }

  handleSharingChange = async (sharingStatus: boolean) => {
    await MessageSender.updateLinkSharing(sharingStatus);
  }

  render() {
    const animation = {
      loop: true,
      autoplay: true,
      animationData: wildlinkAnimation,
      rendererSettings: {
        preserveAspectRatio: "xMidYMid slice"
      }
    };

    return (
      <div id="share-canvas">
        <div className="share-banner-container">
          <Lottie options={animation} height={150} />
        </div>
        <div className="share-title">
          <p>Get cashback when your friends shop with your links</p>
        </div>
        <div className="share-details-list">
          <ShareDetail number="1" text="Browse a covered retailer like usual" />
          <ShareDetail number="2" text="Copy and paste URLs into emails, texts, or on social media" />
          <ShareDetail number="3" text='Share with friends and get cashback when they make purchases' />
        </div>
        <div className="share-switch-container">
          <SwitchComponent
            defaultValue={this.state.isSharingEnabled}
            onColor="#3ae0c0"
            offColor="#edf2f8"
            handleToggle={this.handleSharingChange}
          />
        </div>
      </div>
    );
  }
}