import React from "react";
import * as _ from 'lodash';
import { CashbackListRetailer, CashbackList, User } from "../../../../../common/interfaces";
import { MessageSender } from '../../../../../common/messaging/message-sender';

import './CashbackList.scss';

const AUTHORIZED_PROVIDERS = ['wildlink'] // Authorized providers ORDERED
const MAX_CASHBACK_RETAILERS_TO_DISPLAY = 8;

interface TrendingBrand {
    retailerId: number;
    retailerUrl: string;
    imageUrl: string;
    cashbackTitle: string;
}

interface CashbackListProps {
    cashbackList: CashbackList;
}

interface CashbackListState {
    user?: User;
}

interface CashbackListRetailerProps {
    trendingBrand: TrendingBrand;
}

class CashbackListRetailerComponent extends React.Component<CashbackListRetailerProps, any> {

    openLink = () => {
        const retailerId = this.props.trendingBrand.retailerId;
        const retailerUrl = this.props.trendingBrand.retailerUrl;
        MessageSender.navigateToRetailerFromCashbackList(retailerId, retailerUrl);
        window.close();
    }

    render() {
        return (
            <div className="cashback-list-retailer-item">
                <button onClick={this.openLink}>
                    <img src={this.props.trendingBrand.imageUrl} alt="" />
                </button>
                <p>{this.props.trendingBrand.cashbackTitle}</p>
            </div>
        );
    }
}

export class CashbackListComponent extends React.Component<CashbackListProps, CashbackListState> {

    constructor(props: CashbackListProps) {
        super(props);
        this.state = {}
    }

    async componentDidMount() {
        const user: User | null = await MessageSender.getUser();
        if (user) {
            this.setState({ user });
        }
    }

    private filterTrendingRetailers(retailers: CashbackListRetailer[]): TrendingBrand[] {
        const trendingBrands: any[] = [];
        // Iterate through all retailers from cashbacklist
        for (const retailer of retailers) {
            // Iterate through all defined authorized providers
            for (const provider of AUTHORIZED_PROVIDERS) {
                // Find a deal with authozied provider
                const deal = _.find(retailer.deals, ['provider', provider]);
                if (deal) {
                    trendingBrands.push({
                        retailerId: parseInt(retailer.retailerId),
                        retailerUrl: deal.defaultUrl.split('url=')[1] || '',
                        imageUrl: retailer.images.textLogo,
                        cashbackTitle: deal.title
                    });
                    break;
                }
            }
        }
        return trendingBrands.slice(0, MAX_CASHBACK_RETAILERS_TO_DISPLAY);
    }

    render() {

        const retailers: CashbackListRetailer[] = this.props.cashbackList.retailers;
        const trendingBrands: TrendingBrand[] = this.filterTrendingRetailers(retailers);
        const trendingBrandsComponents = trendingBrands.map((trendingBrand: TrendingBrand) => {
            return (
                <CashbackListRetailerComponent trendingBrand={trendingBrand} />
            );
        })

        return (
            <div id="cashback-list-component">
                <div className="home-earny-logo">
                    <img src="images/earny-logo.svg" alt="earny-logo" />
                </div>
                <p className="cashback-list-title">This retailer doesn’t have any cashback offers. Shop at the following retailers instead:</p>
                <div id="cashback-retailer-list">
                    {trendingBrandsComponents}
                </div>
            </div>
        );
    }
}