import React from "react";
import { PopupMixpanelService } from "../../../../../services/popup-mixpanel.service";
import { MessageSender } from "../../../../../../common/messaging/message-sender";
import { SubscriptionCard } from "../ProfileCard";
import { EarnyPlusText } from "../../../../../../common/react/EarnyPlusTest/EarnyPlusText";
import { FadeAnimation } from "../../../../../../common/react/FadeAnimation/FadeAnimation";

import freeSubscriptionAnimation from '../../../../../public/lotties/profile-free-subscription-animation.json';

export class FreeSubscriptionCard extends React.Component<{ enabled: boolean }, { show: boolean, showConfirmation: boolean }> {

    constructor(props: any) {
      super(props);

      this.state = {
        show: this.props.enabled,
        showConfirmation: false
      };
    }

    handleCTAClicked = async () => {
      PopupMixpanelService.trackFreeSubscriptionActionPressed();

      if (await MessageSender.requestActivateFreeEarnyPlus()) {
        this.setState({
          show: false,
          showConfirmation: true
        });
      }
    }

    render() {
      const style = {
        minHeight: !this.state.show && !this.state.showConfirmation ? '0px' : '375px'
      };

      return (
        <div id="free-subscription-card" style={style}>
          {this.state.show &&
            <SubscriptionCard
              onCTAClicked={this.handleCTAClicked}
              bannerImg={'images/profile/subscription-card-green.svg'}
              lottieAnimationData={freeSubscriptionAnimation}
              ctaText={'Activate'}
            >
              <EarnyPlusText fontSize={20} fontWeight={700} textBefore={'Get 1 FREE month of '} />
              <p>Earn 2X cashback for a month on us and see how much you can save</p>

            </SubscriptionCard>
          }

          <FadeAnimation show={this.state.showConfirmation}>
            <SubscriptionCard
              onCTAClicked={() => this.setState({ showConfirmation: false })}
              bannerImg={'images/profile/subscription-card-success.svg'}
              ctaText={'Okay'} >
              <EarnyPlusText textBefore={'Free month of'} textAfter={'activated!'} fontSize={18} fontWeight={700} />
              <p>You'll now see 2X cashback rates when you shop on desktop or in the app</p>
            </SubscriptionCard>
          </FadeAnimation>

        </div>
      );
    }
  }