import React from "react";
import { PopupMixpanelService, Screen } from "../services/popup-mixpanel.service";


export abstract class PopupScreen<P, S> extends React.Component<P, S> {

    private screen: Screen;

    constructor(props: P, screen: Screen) {
        super(props);
        this.screen = screen;
    }

    componentDidMount() {
        PopupMixpanelService.setScreen(this.screen);
    }
}