import React from 'react';

import './SwitchComponent.scss';

interface SwitchComponentProps {
    defaultValue: boolean;
    onColor: string;
    offColor: string;
    handleToggle: (sharingStatus: boolean) => any;
}

interface SwitchComponentState {
    mounted: boolean;
    isOn: any;
}

export class SwitchComponent extends React.Component<SwitchComponentProps, SwitchComponentState> {

    constructor(props: SwitchComponentProps) {
        super(props);
        this.state = {
            mounted: false,
            isOn: this.props.defaultValue
        }
    }

    componentDidUpdate(prevProps: SwitchComponentProps) {
        if (prevProps.defaultValue !== this.props.defaultValue) {
            this.setState({ isOn: this.props.defaultValue });
        }
    }

    handleChange = () => {
        const newVal = !this.state.isOn;
        this.setState({ isOn: newVal, mounted: true });
        this.props.handleToggle(newVal);
    }

    render() {
        const color = this.state.isOn ? this.props.onColor : this.props.offColor;

        return (
            <>
                <input
                    checked={this.state.isOn}
                    onChange={this.handleChange}
                    className="react-switch-checkbox"
                    id={`react-switch-new`}
                    type="checkbox"
                />
                <label
                    style={{ background: color }}
                    className="react-switch-label"
                    htmlFor={`react-switch-new`}
                >
                    <span className={this.state.mounted ? 'react-switch-button mounted' : 'react-switch-button'} />
                </label>
            </>
        );
    }
};
