import EY from "../../common/EY";
import { Retailer } from "../../common/interfaces";
import { MessageSender } from "../../common/messaging/message-sender";
import { Formatters } from "../../common/utils/formatters";

export type Screen = 'login' | 'home' | 'product-details' | 'profile' | 'watchlist' | 'share';

export class PopupMixpanelService {

    private static screen: Screen;

    /***
     * Nothing is being tracked directly from the popup, the background script handles it
     * so we send it a message to request a track
     */
    private static async requestTrack(eventName: string, data?: any) {
        try {
            const payload = {
                viewType: EY.ChromeViewTypes.CHROME_VIEW_TYPE_POPUP,
                screen: PopupMixpanelService.screen,
                ...data
            };

            await MessageSender.requestTrackMixpanelEvent(eventName, payload);
        } catch (error) {
            console.error(`Error tracking mixpanel event ${eventName}`, error.message);
        }
    }

    public static setScreen(screen: Screen) {
        PopupMixpanelService.screen = screen;
        this.trackScreenPresented(screen);
    }

    public static trackChromeExtensionPopupOpened() {
        this.requestTrack(EY.MixpanelEvents.EVENT_CHROME_EXTENSION_POPUP_OPENED);
    }

    public static trackScreenPresented(screen: Screen) {
        this.requestTrack(EY.MixpanelEvents.EVENT_SCREEN_PRESENTED, { screenName: screen });
    }

    public static trackLoginButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_LOGIN_BUTTON_PRESSED);
    }

    public static trackSignupButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_SIGNUP_BUTTON_PRESSED);
    }

    public static trackLinkSharingTurnedOn() {
        this.requestTrack(EY.MixpanelEvents.EVENT_LINK_SHARING_SETTINGS_TURNED_ON);
    }

    public static trackLinkSharingTurnedOff() {
        this.requestTrack(EY.MixpanelEvents.EVENT_LINK_SHARING_SETTINGS_TURNED_OFF);
    }

    public static trackActivateCashbackPressed(retailer: Retailer) {
        const payload = Formatters.formatRetailerForMixpanel(retailer);
        this.requestTrack(EY.MixpanelEvents.EVENT_ACTIVATE_CASHBACK_BUTTON_PRESSED, payload);
    }

    public static trackActivateCashbackUpsellPressed(retailer?: Retailer) {
        const payload = retailer ? Formatters.formatRetailerForMixpanel(retailer) : null;
        this.requestTrack(EY.MixpanelEvents.EVENT_ACTIVATE_CASHBACK_UPSELL_BUTTON_PRESSED, payload);
    }

    public static trackHelpButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_HELP_BUTTON_PRESSED);
    }

    public static trackSettingsButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_SETTINGS_BUTTON_PRESSED);
    }

    public static trackEarnyPlusButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_EARNY_PLUS_BUTTON_PRESSED);
    }

    public static trackiOSAppStoreButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_IOS_APP_STORE_BUTTON_PRESSED);
    }

    public static trackAndroidAppStoreButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_ANDROID_APP_STORE_BUTTON_PRESSED);
    }

    public static trackProductDetailsAddToWatchlistCTAPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_PRODUCT_DETAILS_ADD_TO_WATCHLIST_CTA_BUTTON_PRESSED);
    }

    public static productDetailsRemoveItemFromWatchlistCTAPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_PRODUCT_DETAILS_REMOVE_ITEM_FROM_WATCHLIST_CTA_PRESSED);
    }

    public static pressedViewPriceHistory() {
        this.requestTrack(EY.MixpanelEvents.EVENT_PRESSED_VIEW_PRICE_HISTORY);
    }

    public static trackWatchlistPresented() {
        this.requestTrack(EY.MixpanelEvents.EVENT_WATCHLIST_PRESENTED);
    }

    public static trackWatchlistEditItemsButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_WATCHLIST_EDIT_ITEMS_BUTTON_PRESSED);
    }

    public static trackWatchlistItemsRemoveButtonPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_WATCHLIST_ITEMS_REMOVE_BUTTON_PRESSED);
    }

    public static trackWatchlistItemSelected() {
        this.requestTrack(EY.MixpanelEvents.EVENT_WATCHLIST_ITEM_SELECTED);
    }

    public static trackCashbackBalanceCashOutPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_CASHBACK_BALANCE_CASH_OUT_PRESSED);
    }

    public static trackFreeSubscriptionActionPressed() {
        this.requestTrack(EY.MixpanelEvents.EVENT_FREE_SUBSCRIPTION_ACTION_PRESSED);
    }

    public static trackFriendReferralsShareViaPressed(via: 'Facebook' | 'Email' | 'Twitter') {
        this.requestTrack(EY.MixpanelEvents.EVENT_FRIEND_REFERRALS_SHARE_VIA_PRESSED, { shareMethod: via });
    }
}