import React from 'react';

import { AreaChart, Area, XAxis, YAxis, Tooltip } from 'recharts';
import { format } from 'date-fns';
import { ChartData } from '../../../../../common/interfaces';

import './PriceHistoryChart.scss';

interface PriceHistoryChartProps {
    chartData?: ChartData[];
}

export class PriceHistoryChart extends React.Component<PriceHistoryChartProps, any> {

    private emptyData: ChartData[] = [{ price: 0, date: new Date() }, { price: 0, date: new Date() }];

    formatDate = (date: string) => {
        // return format(new Date(date), 'MMM d');
        return format(new Date(date), 'MM/dd');

    }

    formatPrice = (price: string) => {
        return `$${price}`;
    }

    formatTooltip = (value: any, name: string, props: any) => {
        return [`$${value}`];
    }

    formatTooltipLabel = (label: any) => {
        return format(new Date(label), 'MMM d yyyy');
    }

    render() {

        /**
         * All this because recharts does not allow empty charts ...
         */
        const isEmpty: boolean = !this.props.chartData || (this.props.chartData.length === 0);
        const data = isEmpty ? this.emptyData : this.props.chartData;
        const showDots: boolean = !isEmpty;
        const stroke: string = isEmpty ? 'none' : '#0070f4';

        return (
            <div id="price-history-chart">
                <AreaChart
                    width={330}
                    height={180}
                    data={data}
                    margin={{ top: 20, right: 20, left: 0, bottom: 10 }}
                >

                    <defs>
                        <linearGradient id="MyGradient" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="0%" stopColor="rgba(0, 112, 244, 0.5)" />
                            <stop offset="100%" stopColor="rgba(0, 112, 244, 0)" />
                        </linearGradient>
                    </defs>


                    {isEmpty === false &&
                        <XAxis
                            axisLine={false}
                            tick={{ fontSize: 13 }}
                            tickLine={false}
                            dataKey="date"
                            interval={'preserveStartEnd'}
                            tickMargin={10}
                            tickFormatter={this.formatDate}
                        />
                    }

                    <YAxis
                        axisLine={false}
                        tick={{ fontSize: 13, fontFamily: 'Montserrat' }}
                        tickLine={false}
                        interval="preserveStartEnd"
                        tickMargin={10}
                        tickFormatter={this.formatPrice}
                    />

                    {isEmpty === false &&
                        <Tooltip
                            labelFormatter={this.formatTooltipLabel}
                            formatter={this.formatTooltip}
                        />
                    }

                    <Area
                        type="linear"
                        dataKey="price"
                        stroke={stroke}
                        strokeWidth="3"
                        fillOpacity="1"
                        fill="url(#MyGradient)"
                        dot={showDots}
                    />

                </AreaChart>

                {isEmpty &&
                    <div className="banner-non-available">
                        Price history unavailable
                    </div>
                }
            </div>
        )
    }
}