import React from "react";
import { Profile, AccountBalance, Email } from "../../../../../common/interfaces";
import { PopupMixpanelService } from "../../../../services/popup-mixpanel.service";
import { BrowserService } from "../../../../../common/services/browser.service";

import './CashoutComponent.scss';

const browserService = new BrowserService();

export class CashoutComponent extends React.Component<{ profile: Profile; canCashout: boolean; }, {}> {

    cashOut = async () => {
        const cashoutEmail: Email = this.props.profile.accountBalance!.cashout.email;
        const recipient: string = encodeURIComponent(cashoutEmail.recipient);
        const body: string = encodeURIComponent(cashoutEmail.body);
        const subject: string = encodeURIComponent(cashoutEmail.subject);
        const url = `mailto:?to=${recipient}&body=${body}&subject=${subject}`;
        browserService.navigateToExternalUrl(url, true);
        PopupMixpanelService.trackCashbackBalanceCashOutPressed();
    }

    render() {

        const accountBalance: AccountBalance | undefined = this.props.profile.accountBalance;
        const cashoutTitle: string = accountBalance ? accountBalance.title : '';
        const cashoutDescription: string = accountBalance ? accountBalance.description : '';

        return (
            <>
                <div id="cashout-description">

                    {this.props.canCashout &&
                        <img src="images/profile/cashout-eligible.svg" alt="cashout-eligible"></img>
                    }

                    {!this.props.canCashout &&
                        <img src="images/profile/cashout-ineligible.svg" alt="cashout-ineligible"></img>
                    }

                    <div className="cashout-texts">
                        <p className="cashout-title">{cashoutTitle}</p>
                        <p className="cashout-description">
                            {cashoutDescription}.&nbsp;
                    <a target="_blank" rel="noopener noreferrer" href="https://support.earny.co/hc/en-us/articles/360036479894-How-do-I-see-and-collect-my-Earnings-balance-">
                                Learn more
                    </a>
                        </p>
                    </div>
                </div>

                <button
                    id="cashout"
                    onClick={this.cashOut}
                    disabled={!this.props.canCashout}
                    className={this.props.canCashout ? 'can-cashout' : ''}>
                    Cash Out
                </button>
            </>
        );
    }
}