import React from "react";
import EY from "../../../../../../common/EY";
import { BrowserService } from "../../../../../../common/services/browser.service";
import { SubscriptionCard } from "../ProfileCard";

import earnyPlusAnimation from '../../../../../public/lotties/profile-subscription-animation.json';
import { PopupMixpanelService } from "../../../../../services/popup-mixpanel.service";

const browserService = new BrowserService();

export class EarnyPlusCard extends React.Component<any, any> {

    handleCTAClick = async () => {
        PopupMixpanelService.trackActivateCashbackUpsellPressed()
        browserService.navigateToExternalUrl(EY.Urls.EARNY_PLAN_URL, true);
        window.close();
    }

    render() {
        return (
            <SubscriptionCard
                onCTAClicked={this.handleCTAClick}
                bannerImg={'images/profile/subscription-card-purple.svg'}
                lottieAnimationData={earnyPlusAnimation}
                ctaText={'Activate'}
            >
                <h3>Now get 2X cashback</h3>
                <p>Don't miss out on earning even more cashback when you shop online</p>
            </SubscriptionCard>
        );
    }
}