import React from "react";
import * as _ from 'lodash';
import { Retailer } from "../../../../../common/interfaces";
import { CashbackRate } from '../../../../../common/interfaces/rate';

import './RetailerActivatedComponent.scss';

const ProductCategoryRate: React.FC<any> = (props: { rate: CashbackRate }) => {
    let rateValue: string = (props.rate.kind === 'PERCENTAGE')
    ? `${props.rate.amount}% cashback`
    : `$${props.rate.amount}`;

    return (
        <div className="rate">
            <h3 className="rate-name">{props.rate.name}</h3>
            <div className="rate-value">{rateValue}</div>
        </div>
    );
}

const RetailerLogo: React.FC<{ imageUrl: string; }> = (props) => {
    const imageUrl = props.imageUrl;
    const isEarnyLogo = imageUrl.includes('earny-logo-eyebrows');
    const className = isEarnyLogo ? 'home-earny-logo' : 'home-retailer-logo';

    return (
        <div className={className}>
            <img src={props.imageUrl} alt="retailer-logo" />
        </div>
    )
}

export class RetailerActivatedComponent extends React.Component<{ retailer: Retailer }, {}> {

    constructor(props: { retailer: Retailer }) {
        super(props);
        this.state = {}
    }

    render() {
        const rates: CashbackRate[] = [];
        // Build a list of rates, and for rates that have a name
        // with commas, break it down for nicer display
        // ex: ['car, beer, toys'] > ['car', 'beer', 'toys]
        this.props.retailer.rates.forEach(rate => {
            rate.name.split(',').forEach((splittedRateName) => {
                const sanitazedName = splittedRateName.replace("and","").trim();
                rates.push({ ...rate, name: sanitazedName });
            });
        });
        // Order alphabetically and push JSX components
        const rateList = _.orderBy(rates, [rate => rate.name.toLowerCase()], ['asc']).map((rate) => {
            return (<ProductCategoryRate rate={rate} />)
        });

        return (
            <div id="retailer-activated">

                <RetailerLogo imageUrl={this.props.retailer.retailerImageUrl} />

                <div className="header">
                    <img src="icons/check-icon.svg" alt="" />
                    <p>{this.props.retailer.activatedText}</p>
                </div>

                {rateList}

                <div className="cashback-exclusion">
                    <p>{this.props.retailer.exclusionText}</p>
                </div>

            </div>
        );
    }
}