import React from 'react';
import styled from 'styled-components';

interface EarnyPlusTextProps {
    fontSize: number,
    fontWeight: number,
    textBefore?: string,
    textAfter?: string
}

/**
 * Just some text that includes Earny+
 */
export const EarnyPlusText: React.FC<EarnyPlusTextProps> = ({ fontSize, fontWeight, textBefore, textAfter }) => {
    const Paragraph = styled.p`
        font-family: 'Montserrat';
        font-size: ${fontSize}px !important;
        font-weight: ${fontWeight} !important;
    `;

    const Span = styled.span`
        vertical-align: top;
        line-height: 14px;
        font-size: 30px;
        font-weight: 900;
        color: #3AE0C0;
    `;

    return (
        <Paragraph className="earny-plus-text">
            {textBefore &&
                <>{textBefore}{' '} </>
            }
            Earny<Span>+</Span>
            {textAfter &&
                <>{' '}{textAfter}</>
            }
        </Paragraph>
    )
}