import { Product } from "../interfaces";

export enum ProductState {
    NO_PRODUCT_PAGE,
    NO_PRODUCT_DETAILS,
    PRODUCT_DETAILS_PENDING,
    PRODUCT_DETAILS_VALID
}

export class ProductStateParser {

    public static getState(product?: Product | null): ProductState {

        if (!product) return ProductState.NO_PRODUCT_PAGE;

        if (product.isPendingProductDetails === true) {
            return ProductState.PRODUCT_DETAILS_PENDING;
        }

        if (product.isProductPage) {
            if (product.productDetails) {
                if (!product.productDetails.name) {
                    return ProductState.PRODUCT_DETAILS_PENDING;
                }
                return ProductState.PRODUCT_DETAILS_VALID;
            }
            return ProductState.NO_PRODUCT_DETAILS;
        }
        return ProductState.NO_PRODUCT_PAGE;
    }
}