import React from "react";

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from 'react-router-dom';

import { HomeScreen } from "../Home/HomeScreen";
import { ShareScreen } from "../Share/ShareScreen";
import { ProfileScreen } from "../Profile/ProfileScreen";
import { BottomNavigation, BottomNavigationRoute } from "./BottomNavigation/BottomNavigation";
import { ProductDetailsScreen } from '../ProductDetails/ProductDetailsScreen';
import { WatchlistScreen } from "../Watchlist/WatchlistScreen";
import { FriendReferralEarnings } from "../Profile/FriendReferralEarnings/FriendReferralEarnings";

import './Main.css';

// ===========================================================================================
// --- Constants/Interfaces
// ===========================================================================================
interface MainProps {

}

interface MainState {

}

// ===========================================================================================
// --- Main
// ===========================================================================================
export class Main extends React.Component<MainProps, MainState> {

  constructor(props: MainProps) {
    super(props);

    this.state = {
      isSharingEnabled: false
    }
  }

  render() {

    const routes: BottomNavigationRoute[] = [
      { key: 1, path: '/home', iconPathUrl: 'icons/home-icon.svg' },
      { key: 2, path: '/product-details', iconPathUrl: 'icons/tag-icon.svg' },
      { key: 3, path: '/watchlist', iconPathUrl: 'icons/heart-icon.svg' },
      { key: 4, path: '/share', iconPathUrl: 'icons/share-icon.svg' },
      { key: 5, path: '/profile', iconPathUrl: 'icons/profile-icon.svg' },
    ]

    return (
      <Router>
        <div className="main-container">
          <Switch>

            <Route path="/home" component={HomeScreen} />

            <Route path="/product-details" component={ProductDetailsScreen} />

            <Route path="/watchlist/details/:itemId" component={ProductDetailsScreen} />
            <Route path="/watchlist" component={WatchlistScreen} />

            <Route path="/share" component={ShareScreen} />

            <Route path="/profile/friend-referral-earnings" component={FriendReferralEarnings} />
            <Route path="/profile" component={ProfileScreen} />

            <Route path="/">
              <Redirect to='/home' />
            </Route>
          </Switch>
        </div>

        <div>
          <BottomNavigation routes={routes} />
        </div>
      </Router>
    );
  }
}
